<script>
    import { createEventDispatcher } from 'svelte';
	import { shuffle } from './utility/CollectionUtility.js';
    import { Wave } from 'svelte-loading-spinners'; 

    import Carousel from 'svelte-carousel';
    import FaAngleRight from 'svelte-icons/fa/FaAngleRight.svelte'
    import FaAngleLeft from 'svelte-icons/fa/FaAngleLeft.svelte'

    import AudioPhrase from './AudioPhrase.svelte';
    import TextPhrase from './TextPhrase.svelte';
    import MultiChoicePhrase from './MultiChoicePhrase.svelte';

    let holdFlip = false;
    const newQuestionDispatcher = createEventDispatcher();

    export let isMobile;

    export let trainingSets;
    export let chosenTrainingSet;

    function preventFlip(){
        holdFlip = true;
        setTimeout(() => holdFlip = false, 500);
    }
    function newQuestionEvent(eventData){
        var nextPageElement = document.getElementById('NextPageCarousel');
        setTimeout(function(){
            nextPageElement.click();
        }, 500);
        newQuestionDispatcher('newQuestion', eventData);
    }
</script>

<style>
    .carousel{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
    }
    .mobileIcon{
        height: 100%;
        width: 35px;
        display: flex;
        justify-content: center;
        vertical-align: center;
        background-color: #5a4ba5;
    }
    .icon{
        height: 100%;
        width: 35px;
        display: flex;
        justify-content: center;
        vertical-align: center;
        background-color: #5a4ba5;
    }
</style>

<div class="carousel">
    {#if chosenTrainingSet === 'none'}
            <span>U kunt een trainingsSet selecteren in de top linkerkant van het scherm.</span>
    {:else if chosenTrainingSet === 'loading'}
        <div>
            <Wave size="60" color="#5a4ba5" unit="px" duration="1s"></Wave>
            <h2>Loading</h2>
        </div>    
    {:else}
    <Carousel
        let:showPrevPage
        let:showNextPage
        on:pageChange={preventFlip}
        >
        <div slot="prev" on:click={showPrevPage} 
        class:mobileIcon={isMobile} 
        class:icon={!isMobile}>
            <FaAngleLeft /> 
        </div>
            {#each shuffle(trainingSets[chosenTrainingSet].data)
                .filter(da => da.type === "multi/choice") as phrase}
                {#if phrase.type === "audio"}
                    <AudioPhrase isMobile={isMobile} holdFlip={holdFlip} phrasesObject={phrase} />
                {:else if phrase.type === "text"}
                    <TextPhrase isMobile={isMobile} holdFlip={holdFlip} phrasesObject={phrase} />
                {:else if phrase.type === "multi/choice"}
                    <MultiChoicePhrase isMobile={isMobile} holdFlip={true} phrasesObject={phrase} on:newQuestion={newQuestionEvent} />
                {/if}
            {/each}

            <div slot="next" id="NextPageCarousel" on:click={showNextPage} 
            class:mobileIcon={isMobile} 
            class:icon={!isMobile}>
                <FaAngleRight /> 
            </div>
    </Carousel>
    {/if}
</div>