<script>
	import './assets/luxacss.min.css';
	import PhraseCarousel from './PhraseCarousel.svelte';
	import MediaQuery from "./MediaQuery.svelte";
	import Progress from './Progress.svelte';
	import DropDown from './DropDown.svelte';

    import { trainingSets } from './data/trainingSets.js';

	let totalAmountOfQuestions = 0;
	let currentAmountOfCorrectQuestions = 0;
    let progress;
	let chosenTrainingSet = 'none';

    function restartFlashCards()
    {
		chosenTrainingSet = 'none';
    }
	function newQuestionEvent(eventData){
		if(totalAmountOfQuestions === 0)
			totalAmountOfQuestions = trainingSets[chosenTrainingSet].data.length;
		
		if(eventData.detail.detail.isCorrect){
			currentAmountOfCorrectQuestions++;
			progress.set(currentAmountOfCorrectQuestions / totalAmountOfQuestions);
		}
	}
	function selectedNewTrainingSetEvent(eventData){
		chosenTrainingSet = 'loading';
		setTimeout(function(){
			progress.set(0);
			chosenTrainingSet = eventData.detail;
			currentAmountOfCorrectQuestions = 0;
			totalAmountOfQuestions = trainingSets[chosenTrainingSet].data.length;
        }, 1500);
	}
	
</script>

<style>
	main {
	  font-family: "Roboto", sans-serif;
	  color: white;
	  background-color: #22212c;
	}
	
	main section {
	  min-height: 100vh;
	  padding: 4rem 0;
	}
	main section .lx-badge {
	color: white;
	background-color: #5a4ba5;
	}
</style>

<div>
	<MediaQuery query="(min-width: 1281px)" let:matches>
		{#if matches}
		<main>
			<section class="has-dflex-center">
				<div class="lx-container-70">
					<div class="lx-row">
						<DropDown PossibleTrainingSets={trainingSets} on:selectedNewTrainingSet={selectedNewTrainingSetEvent}/>
						<span class="lx-badge" on:click={restartFlashCards}>Restart</span>
						<Progress bind:progress={progress}/>
						<PhraseCarousel isMobile={false} bind:chosenTrainingSet trainingSets={trainingSets} on:newQuestion={newQuestionEvent} />
					</div>
				</div>
			</section>
		</main>
		{/if}
	</MediaQuery>
	
	<MediaQuery query="(max-width: 1280px)" let:matches>
		{#if matches}
		<main>
			<section class="has-dflex-center">
				<div class="lx-container-100">
					<div class="lx-row">
						<DropDown PossibleTrainingSets={trainingSets} on:selectedNewTrainingSet={selectedNewTrainingSetEvent}/>
						<span class="lx-badge bs-sm" on:click={restartFlashCards}>Restart</span>
						<Progress bind:progress={progress}/>
						<PhraseCarousel isMobile={true} bind:chosenTrainingSet trainingSets={trainingSets} on:newQuestion={newQuestionEvent}/>
					</div>
				</div>
			</section>
		</main>
		{/if}
	</MediaQuery>
</div>



