<script>
    import { createEventDispatcher } from 'svelte';

    const newTrainingSetSelectedEvent = createEventDispatcher();
    export let PossibleTrainingSets = [];

    function onClickDropDownItem(item){
        newTrainingSetSelectedEvent('selectedNewTrainingSet', item);
    }
</script>

<style>
.lx-nav,
.lx-nav-item.has-dropdown,
.lx-nav-item.dropdown,
.lx-nav-menu.is-active 
{
    z-index: 999;
    background-color: #22212c;
}
.lx-nav-item-button{
    width: 100%;
}
.lx-nav-item-button:hover{
    background-color: #5a4ba5;
}
</style>

<div class="lx-nav-item has-dropdown">
    <span on>&nbsp;Training Sets</span>
    <div class="lx-nav-item dropdown">
        {#each Object.keys(PossibleTrainingSets) as trainingSet}
            <button class="lx-nav-item-button" on:click={onClickDropDownItem(trainingSet)}>{PossibleTrainingSets[trainingSet].name}</button>
        {/each}
    </div>
</div>